	module.exports = {"pathContext":{"docHtml":"<h1 id=\"understand-the-basics\"><a href=\"#understand-the-basics\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Understand The Basics</h1>\n<p>Before effectively using Argo CD, it is necessary to understand the underlying technology that the platform is built on. It is also necessary to understand the features being provided to you and how to use them. The section below provides some useful links to build up this understanding.</p>\n<h2 id=\"learn-the-fundamentals\"><a href=\"#learn-the-fundamentals\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Learn The Fundamentals</h2>\n<ul>\n<li>\n<p>Go through the online Docker and Kubernetes tutorials</p>\n<ul>\n<li><a href=\"https://medium.freecodecamp.org/a-beginner-friendly-introduction-to-containers-vms-and-docker-79a9e3e119b\">A Beginner-Friendly Introduction to Containers, VMs and Docker</a></li>\n<li><a href=\"https://courses.edx.org/courses/course-v1:LinuxFoundationX+LFS158x+2T2017/course/\">Introduction to Kubernetes</a></li>\n<li><a href=\"https://kubernetes.io/docs/tutorials/\">Tutorials</a></li>\n<li><a href=\"https://katacoda.com/courses/kubernetes/\">Hands on labs</a></li>\n</ul>\n</li>\n<li>\n<p>Depending on how you plan to template your applications:</p>\n<ul>\n<li><a href=\"https://kustomize.io\">Kustomize</a> </li>\n<li><a href=\"https://helm.sh\">Helm</a></li>\n<li><a href=\"https://ksonnet.io\">Ksonnet</a> </li>\n</ul>\n</li>\n<li>\n<p>If you're integrating with Jenkins:</p>\n<ul>\n<li><a href=\"https://jenkins.io\">Jenkins User Guide</a></li>\n</ul>\n</li>\n</ul>","docPath":"argo-cd/docs/understand_the_basics","proj":"argo-cd"}}

